import { z } from "zod";

// ✅ Define Department schema
export const departmentSchema = z.object({
  id: z.string(),
  name: z.string(),
  description: z.string(),
  imageUrl: z.string().url()
});

// ✅ Export Department type
export type Department = z.infer<typeof departmentSchema>;

// ✅ Existing application schema
export const insertApplicationSchema = z.object({
  firstName: z.string().min(1, "First name is required").min(2, "First name must be at least 2 characters"),
  lastName: z.string().min(1, "Last name is required").min(2, "Last name must be at least 2 characters"),
  email: z.string().min(1, "Email is required").email("Please enter a valid email address"),
  phone: z.string().min(1, "Phone number is required").min(10, "Phone number must be at least 10 characters"),
  program: z.string().min(1, "Please select a program"),
  department: z.string().min(1, "Please select a department"),
  education: z.string().min(1, "Please select your educational background"),
  additionalInfo: z.string().optional(),
  agreesToTerms: z.boolean().refine(val => val === true, {
    message: "You must agree to the terms and conditions"
  })
});

export type InsertApplication = z.infer<typeof insertApplicationSchema>;

// ✅ Dropdown options
export const programOptions = [/* unchanged */] as const;
export const educationOptions = [/* unchanged */] as const;
export const departmentOptions = [/* unchanged */] as const;
